import React, {useState} from "react";
import {Fade} from "react-reveal";
import {useLocalStorage} from "../../hooks/useLocalStorage";
import {feedbacks} from "../../portfolio";

export default function ClientFeedbacks() {
  const darkPref = window.matchMedia("(prefers-color-scheme: dark)");
  const [isDark] = useLocalStorage("isDark", darkPref.matches);
  const [showAll, setShowAll] = useState(false);

  return (
    <div className={isDark ? "dark-mode main" : "main"} id="skills">
      <div className="skills-main-div">
        <Fade left duration={1000}>
          <div
            style={{
              display: "flex",
              flexWrap: "wrap",
              justifyContent: "space-evenly"
            }}
          >
            {feedbacks.slice(0, showAll ? undefined : 5).map(item => {
              return (
                <div
                  key={item.name}
                  style={{
                    width: "350px",
                    padding: 10,
                    // border: "1px solid grey",
                    boxShadow: "1px 1px 30px #C0C0C0",
                    margin: 10,
                    marginTop: 20,
                    borderRadius: "20px 0px 20px 0px"
                  }}
                >
                  <div style={{display: "flex"}}>
                    <div
                      style={{
                        width: "70px",
                        height: "70px",
                        borderRadius: "100%",
                        overflow: "hidden"
                      }}
                    >
                      <img
                        style={{height: "100%", width: "100%"}}
                        src={`/${
                          item.imageURL || "client-photos/wordpressdesgin.jpg"
                        }`}
                        alt={item.name}
                      />
                    </div>
                    <div style={{paddingLeft: 10}}>
                      <div>
                        <b>{item.name}</b>
                      </div>
                      <div>
                        <span style={{fontSize: 14}}>{item.country}</span>
                        &nbsp;&nbsp;<span style={{color: "orange"}}></span>
                      </div>
                    </div>
                  </div>
                  <hr />
                  <div style={{fontSize: 15, textAlign: "justify"}}>
                    {item.comments.map(comment => {
                      return (
                        <div key={comment}>
                          <b></b>
                          <span style={{color: "#444444"}}>{comment}</span>
                          <b></b>
                        </div>
                      );
                    })}
                  </div>
                </div>
              );
            })}
          </div>
        </Fade>
      </div>
      {!showAll && (
        <button className="main-button" onClick={() => setShowAll(true)}>
          See all
        </button>
      )}
    </div>
  );
}
