import React from "react";
import {Fade} from "react-reveal";
import {skillList, speakingLanguages, techStack} from "../../portfolio";
import "./Progress.scss";

export default function StackProgress() {
  if (techStack.viewSkillBars) {
    return (
      <Fade bottom duration={1000} distance="20px">
        <div className="skills-container">
          <div className="skills-bar">
            <h1 className="skills-heading">Programming Languages</h1>
            {techStack.experience.map((exp, i) => {
              const progressStyle = {
                width: exp.progressPercentage
              };
              return (
                <div key={i} className="skill">
                  <p>{exp.Stack}</p>
                  <div className="meter">
                    <span style={progressStyle}></span>
                  </div>
                </div>
              );
            })}
          </div>
          <div className="skills-bar">
            <h1 className="skills-heading">Speaking Language</h1>
            {speakingLanguages.experience.map((exp, i) => {
              const progressStyle = {
                width: exp.progressPercentage
              };
              return (
                <div key={i} className="skill">
                  <p>{exp.Stack}</p>
                  <div className="meter">
                    <span style={progressStyle}></span>
                  </div>
                </div>
              );
            })}
            <div>
              <h1 className="skills-heading">Skills</h1>
              <div
                style={{
                  display: "flex",
                  flexWrap: "wrap",
                  lineHeight: "initial"
                }}
              >
                {skillList.experience.map(obj => {
                  return (
                    <div className="skill-chip" key={obj.Stack}>
                      {obj.Stack}
                      <div
                        className="skill-chip-background"
                        style={{width: obj.progressPercentage}}
                      ></div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>

          {/* 
          <div className="skills-image">
            {illustration.animated ? (
              <DisplayLottie animationData={Build} />
            ) : (
              <img
                alt="Skills"
                src={require("../../assets/images/skill.svg")}
              />
            )}
          </div> */}
        </div>
      </Fade>
    );
  }
  return null;
}
